// Breadcrumb Navigation Styles
.wp-block-caes-hub-breadcrumbs {

	.breadcrumb-navigation {
		font-size: 0.875rem;
		line-height: 1.5;
	}

	.breadcrumb-list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		list-style: none;
		margin: 0;
		padding: 0;
		gap: 0;
	}

	.breadcrumb-list-item {
		display: inline-flex;
		align-items: center;
		margin: 0;
		padding: 0;

		// Add separator after each item except the last
		&:not(:last-child)::after {
			content: "/";
			color: inherit;
			opacity: 0.6;
			margin: 0 0.5em;
			user-select: none;
		}
	}

	.breadcrumb-item {
		display: inline-flex;
		align-items: center;
	}

	// Link styles
	.breadcrumb-home,
	.breadcrumb-link {
		color: inherit;
		text-decoration: underline;
		text-decoration-thickness: 1px;
		text-underline-offset: 0.125em;
		transition: color 0.2s ease, text-decoration-color 0.2s ease;

		&:hover,
		&:focus {
			text-decoration-thickness: 2px;
		}

		&:focus {
			outline: 2px solid currentColor;
			outline-offset: 2px;
		}
	}

	// Current page (non-link)
	.breadcrumb-current {
		color: inherit;
	}

	// Responsive adjustments
	@media (max-width: 480px) {
		.breadcrumb-navigation {
			font-size: 0.8125rem;
		}

		.breadcrumb-list-item:not(:last-child)::after {
			margin: 0 0.25em;
		}
	}
}

// Editor-specific styles
.wp-block-caes-hub-breadcrumbs {

	.breadcrumb-preview-notice {
		margin-bottom: 1rem;

		.components-notice__content {
			font-size: 0.875rem;
		}
	}
}

// Print styles
@media print {
	.wp-block-caes-hub-breadcrumbs {
		.breadcrumb-list {
			display: none;
		}
	}
}

// Mobile-specific styles
@media (max-width: 768px) {
    // Hide completely on publications
    .breadcrumb-navigation--hidden {
        display: none;
    }
    
    .breadcrumb-list {
        flex-wrap: nowrap;
    }
    
    .breadcrumb-list-item--middle {
        display: none;
    }
    
    // Only show ellipsis when there are middle items to collapse
    .breadcrumb-navigation--has-middle .breadcrumb-list-item--parent::before {
        content: "â€¦";
        opacity: 0.6;
        margin-right: 0.5em;
    }
    
    .breadcrumb-list-item--current .breadcrumb-current {
        display: inline-block;
        max-width: 200px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        vertical-align: bottom;
    }
}